export default {
  name: "Mint",
  props: {
    showMint: {
      type: Boolean,
      default: false
    },
    isWhite: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      MintNum: 1,
      visables: this.showMint,
    }
  },
  watch: {
    showMint(n) {
      this.visables = n;
    },
  },
  methods: {
    subtraction() {
      if (this.MintNum > 1) {
        this.MintNum--;
      } else {
        this.$toast("Mint数量不能小于1");
      }
    },
    Addition() {
      let MintNum=5;
      if(this.isWhite){
        MintNum=10
      }
      if (this.MintNum < MintNum) {
        this.MintNum++;
      }else{
        this.$toast(this.isWhite?"白名单用户Mint数量不能大于10个":"非白名单用户Mint数量不能大于5个");
      }
    },
    close() {
      this.$emit("closeMint", false);
      this.MintNum=1;
    },
    MintToken(MintNum) {
      if (MintNum >= 1) {
        this.$emit("closeMint", MintNum)
      } else {
        this.$toast.fail("Mint数量不能小于1");
      }
    }
  },
  computed: {
    MATIC() {
      return this.MintNum * 100;
    }
    // MATICPRICE() {
    //   return this.MintNum * 100 * 6.6
    // }
  }
}

