export default {
  name: "MintSuccess",
  props: {
    showMintSuccess: {
      type: Boolean,
      default: false
    },
    MintData: {
      type: Array,
      default: () => []
      }
  },
  data() {
    return {
      MintNum: 1,
      visables: this.showMintSuccess,
    }
  },
  watch: {
    showMintSuccess(n) {
      this.visables = n;
    },
  },
  methods: {
    close() {
      this.$emit("closeMintSuccess", false);      }
  }
}

