export default {
  name: "InvitedCard",
  props: {
    showInvitedlist: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      MintNum: 0,
      visables: this.showInvitedlist,
    }
  },
  watch: {
    showInvitedlist(n) {
      this.visables = n;
    },
  },
  methods: {
    close() {
      this.$emit("closeInvitedCard", false);
    }
  }
}

